const coordinates = [
  50.40264008081105,
  30.40875833161278,
];

// –£—Å—Ç–∞–Ω–æ–≤–∫–∞ —Ü–µ–Ω—Ç—Ä–∞ –∫–∞—Ä—Ç—ã –Ω–∞ –ö–∏–µ–≤–µ
const map = L.map('map').setView(coordinates, 13);

L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
  maxZoom: 18,
  titleSize: 512,
}).addTo(map);

// –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –º–∞—Ä–∫–µ—Ä–∞ —Å –∞–¥—Ä–µ—Å–æ–º
L.marker(coordinates).addTo(map);
